body{
    font: 12rem/18rem 'Montserrat', 'Arial', sans-serif;
    background: radial-gradient(65.36% 217.76% at -49.91% 85.33%, #413A33 0%, rgba(23, 23, 23, 0.00) 77.84%), radial-gradient(135.95% 54.88% at 76.62% 34.99%, #413A33 0%, #171717 77.84%);

    @media only screen and (max-width : 660px) {
        background: var(--tt, #121212);
    } // @media only screen and (max-width : 660px)
}

h1{

}

h2{
    color: #FFF;
    text-align: center;
    font-size: 45rem;
    font-style: normal;
    font-weight: 700;
    line-height: 51rem;
    margin-bottom: 50rem;
}

.h2-description {
    color: #FFF;
    text-align: center;
    font-size: 18rem;
    font-style: normal;
    font-weight: 400;
    line-height: 148.9%;
