    }
}

.container{
    width: 1440rem;
    margin: 0 auto;
    box-sizing: border-box;

    @media only screen and (max-width : 660px) {
        margin-left: 15rem;
        margin-right: 15rem;
        width: calc(100% - 30rem);
    }
}



