.btn{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    box-sizing: border-box;
    white-space: nowrap;
}

.btn-gold {
    background: #D6B16A;
    color: var(--tt, #121212);
    font-size: 14rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.285;

    height: 53rem;
    padding-left: 2.9em;
    padding-right: 2.9em;
    box-sizing: border-box;
    transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

    &:hover{
        opacity: 0.7;
    }
}

.btn-gold-2 {
    background: linear-gradient(158deg, #D6B16A 32.35%, #FFE5B1 52.08%, #FFCF72 71.10%);
    box-shadow: 0rem 4rem 61rem 0rem rgba(0, 0, 0, 0.88);

    color: var(--tt, #121212);
    font-size: 21rem;
    font-style: normal;
    font-weight: 600;
    line-height: 32.485rem;

    height: 80rem;
    padding-left: 2.9em;
    padding-right: 2.9em;
    box-sizing: border-box;
    transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

    &:hover{
        opacity: 0.7;
    }
}

.btn-brown {
    background: #D6B16A;

    font-size: 23rem;
    font-weight: 600;
    color: #FFF;
    line-height: 1.2;

    min-width: 191rem;
    height: 70rem;
    padding-left: 2.285714285714286em;
    padding-right: 2.285714285714286em;

    transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

    &:hover{
        opacity: 0.7;
    }
}






