.topbar {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;

    width: 100%;
    padding-top: 37rem;
    padding-bottom: 37rem;
    box-sizing: border-box;
}

.topbar-flex {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    
}

.topbar-logo {
    display: block;
    width: 194rem;

    transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

    &:hover{
        opacity: 0.7;
    }
}

.topbar-tels {
    margin-left: auto;
    margin-right: 35rem;

    a{
        display: block;
        color: #FFF;
        font-size: 23rem;
        font-style: normal;
        font-weight: 600;
        line-height: 1.285;

        transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;

        &:hover{
            opacity: 0.7;
        }
    }
}




@media only screen and (max-width : 660px) {
    .topbar {
        // height: 50px;
        padding-top: 11rem;
        padding-bottom: 10rem;
        box-sizing: border-box;
    }
    
    .topbar-flex {
        .btn.btn-gold{
            display: none;
        }
    }
    
    .topbar-logo {
        width: 128rem;
    }
    
    .topbar-tels {
        margin-right: 0;
    
        a{
            background: url(../images/ico-tel.svg) no-repeat center center;
            background-size: 100% auto;

            padding-top: 23rem;
            padding-left: 23rem;
            width: 23rem;
            height: 23rem;
            box-sizing: border-box;

            overflow: hidden;
        }
    }
} // @media only screen and (max-width : 660px)


















.block-first {
    position: relative;
    padding-top: 186rem;
    padding-bottom: 100rem;
    box-sizing: border-box;

    .container{
        position: relative;
        z-index: 5;
    }
}

.block-first-image{
    position: absolute;
    top: 0;
