input[type="checkbox"].custom,
input[type="radio"].custom{
	display: none;
}

label.checkbox{
	border: 2rem solid #1F9BD7;
	border-radius: 2rem;
	box-sizing: border-box;
	width: 20rem;
	height: 20rem;
	position: relative;

	&::before{
		content: ' ';
		display: block;
		
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		width: 10rem;
		height: 10rem;

		background: #1F9BD7;
		border-radius: 2rem;
		box-sizing: border-box;

		opacity: 0;
		transition: all 0.2s cubic-bezier(0.42, 0, 0.58, 1.0) 0s;
	}

	input[type="checkbox"]:checked + &{
		&::before{
			opacity: 1;
		}
	}
}









